.saved-screen {
  display: flex;
  flex-direction: column;
  height: 100vh; /* Altura total da tela */
  background-color: #f5f5f5;
}

/* Botão fixo no topo */
.fixed-button {
  position: sticky;
  top: 0;
  z-index: 10;
  background-color: #f5f5f5; /* Cor de fundo para evitar sobreposição */
  padding: 10px;
  text-align: center;
}

/* Contêiner rolável */
.list-container {
  flex: 1; /* Permite que a lista ocupe o espaço restante */
  overflow-y: auto; /* Ativa o scroll vertical */
  padding: 10px;
  background-color: transparent;
}

.content {
  padding: 10px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.card-saved {
  background-color: #ffffff;
  width: 80%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.text {
  font-size: 16px;
  color: #931d89;
  margin-bottom: 10px;
}

.number-list {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 10px;
}

.number-circle {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #931d89;
  color: white;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  font-size: 14px;
}

.share-button {
  background: none;
  border: none;
  color: #ff1f1e;
  cursor: pointer;
  margin-top: 10px;
}

.share-button .material-icons {
  font-size: 12px;
}

.line {
  border: none;
  height: 1px;
  background-color: #ccc;
  margin: 10px 0;
}
